






















import { NavBar, HomeSider, MenuTab, MainBody } from "./components";
import { Component, Vue } from "vue-property-decorator";
@Component({
  components: {
    NavBar,
    HomeSider,
    MenuTab,
    MainBody
  }
})
export default class Index extends Vue {
  isCollapse: boolean = false;
  changeSiderStatus() {
    this.isCollapse = !this.isCollapse;
  }
}
